:local .divIcon {
  position: relative;
  width: 45px !important;
  height: 45px !important;
  background-color: white; }

:local .icon {
  position: absolute;
  top: 2px;
  left: 10px;
  font-size: 25px; }

@media (max-width: 576px) {
  :local .divIcon {
    position: relative;
    width: 40px !important;
    height: 40px !important;
    background-color: white; }
  :local .icon {
    position: absolute;
    top: 0px;
    left: 8px;
    font-size: 25px; } }
